import React from 'react';

class CommentsList extends React.Component {
    render() {
        const { comments } = this.props;
        return (
            <>
                <h3>Comments:</h3>
                {comments.map((comment, key) => (
                    <div className="comment" key={key}>
                        <h4>{comment.userName}</h4>
                        <p>{comment.text}</p>
                    </div>
                ))}
            </>
        );
    }
};

export default CommentsList;