import React from 'react';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';
import ArticlesList from '../components/ArticleList';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';

class ArticlePage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            upvotes: 0,
            comments: [],
            article: null,
        };
    }

    async componentDidMount() {
        const { match } = this.props;
        const articleName = match.params.name;

        fetch(`/api/articles/${articleName}`)
          .then(res => res.json())
          .then(json => {
              this.setArticleInfo(json);
          });
    }

    setArticleInfo = (json) => {
        this.setState({ 
            article: json,
            upvotes: json.upvotes,
            comments: json.comments,
        });
    }

    render() {

        const { match } = this.props;
        const { upvotes, comments } = this.state;
        const articleName = match.params.name;
        const article = articleContent.find(article => article.name === articleName);
        // const article = await this.fetchData(articleName);
        if (!article) {
            return <NotFoundPage></NotFoundPage>
        }
        console.log("comments,",comments)

        const otherArticles= articleContent.filter(article => article.name !== articleName)
        return (
            <>
                <h1>{article.title}</h1>
                <UpvoteSection
                    articleName={articleName}
                    upvotes={upvotes}
                    setArticleInfo={this.setArticleInfo}
                />
                {article.content.map((paragraph, key) => (
                    <p key={key}>{paragraph}</p>
                ))}
                <h3>Other Articles</h3>
                <ArticlesList articles={otherArticles} />
                <CommentsList comments={comments} />
                <AddCommentForm 
                    articleName={articleName}
                    setArticleInfo={this.setArticleInfo}
                />
            </>
        );
    }
};

export default ArticlePage;