import React from 'react';

class UpvoteSection extends React.Component {


    upvoteArticle = async() => {
        const { articleName, setArticleInfo } = this.props;
        fetch(`/api/articles/${articleName}/upvote`, {
            method: 'post',
        }).then(res => res.json())
        .then(json => {
            setArticleInfo(json);
        }); 
    }

    render() {
        const { upvotes } = this.props;

        return (
            <>
                <div className="upvotes-section">
                    <button onClick={() => this.upvoteArticle()}>Add Upvote</button>
                    <p>This post has been upvoted {upvotes} times</p>
                </div>

            </>
        );
    }
};

export default UpvoteSection;